import React,  { Suspense, useRef, useState, useEffect } from "react"
import { Canvas, useFrame } from "@react-three/fiber"
import { ContactShadows, Environment, useGLTF, OrbitControls} from "@react-three/drei"
import { HexColorPicker } from "react-colorful"
import { proxy, useSnapshot } from "valtio"
import "./App.css"


const state = proxy({
  current: null,
  items: {
    Metal: "#ffffff",
    lace: "#00FF00",
    heel: "#000000",
    sole: "#000000",
    sole_rim: "#00FF00",
    detail: "#ffffff",
    main: "#00FF00",
    inside: "#000000",
    front: "#000000",
    tongue: "#ffffff",
    insole: "#242020",
  },
})




export function Model(props) {
  const ref = useRef()
  const snap = useSnapshot(state)
  const { nodes, materials } = useGLTF("/shoe.glb");

  useFrame((state) => {
    const t = state.clock.getElapsedTime()
    ref.current.rotation.z = -0.2 - (1 + Math.sin(t / 1.5)) / 20
    ref.current.rotation.x = Math.cos(t / 4) / 8
    ref.current.rotation.y =  Math.PI / 2 + Math.PI /4
    ref.current.position.y = (1 + Math.sin(t / 1.5)) / 30
  })




  return (
    <group 
     ref={ref}
      {...props} 
    
       dispose={null}
>

      <mesh
        castShadow
        receiveShadow
        geometry={nodes.metal_lace.geometry}
        material={materials.metal_lace}
        material-color={snap.items.Metal}
        position={[-0.02, 0.04, 0.02]}
        rotateY={Math.PI / 2}
      />
      <mesh
        castShadow
        receiveShadow
        geometry={nodes.lace.geometry}
        material={materials.lace}
        material-color={snap.items.lace}
        position={[-0.02, 0.04, 0.02]}
      />
      <mesh
        castShadow
        receiveShadow
        geometry={nodes.heel.geometry}
        material={materials.heel}
        position={[-0.02, 0.04, 0.02]}
        material-color={snap.items.heel}
      />
      <mesh
        castShadow
        receiveShadow
        geometry={nodes.sole.geometry}
        material={materials.sole}
        position={[-0.02, 0.04, 0.02]}
        material-color={snap.items.sole}
      />
      <mesh
        castShadow
        receiveShadow
        geometry={nodes.sole_rim.geometry}
        material={materials.sole_rim}
        position={[-0.02, 0.04, 0.02]}
        material-color={snap.items.sole_rim}
      />
      <mesh
        castShadow
        receiveShadow
        geometry={nodes.detail.geometry}
        material={materials.detail}
        position={[-0.02, 0.04, 0.02]}
        material-color={snap.items.detail}
      />
      <mesh
        castShadow
        receiveShadow
        geometry={nodes.main.geometry}
        material={materials.main}
        position={[-0.02, 0.04, 0.02]}
        material-color={snap.items.main}
      />
      <mesh
        castShadow
        receiveShadow
        geometry={nodes.inside.geometry}
        material={materials.inside}
        position={[-0.02, 0.04, 0.02]}
        material-color={snap.items.inside}
      />
      <mesh
        castShadow
        receiveShadow
        geometry={nodes.front.geometry}
        material={materials.front}
        position={[-0.02, 0.04, 0.02]}
        material-color={snap.items.front}
      />
      <mesh
        castShadow
        receiveShadow
        geometry={nodes.tongue.geometry}
        material={materials.tongue}
        position={[-0.02, 0.04, 0.02]}
        material-color={snap.items.tongue}
      />
      <mesh
        castShadow
        receiveShadow
        geometry={nodes.insole.geometry}
        material={materials.insole}
        position={[-0.02, 0.04, 0.02]}
        material-color={snap.items.insole}
      />
    </group>
  );
}

function Picker() {
  const snap = useSnapshot(state)
  return (
    <div style={{ display: snap.current ? "block" : "none" }}>
      <HexColorPicker className="picker" color={snap.items[snap.current]} onChange={(color) => (state.items[snap.current] = color)} />
    
    </div>
  )
}


useGLTF.preload("/shoe.glb");
export default function App() {
  return (
    <div id="model">
      <Canvas shadows camera={{ position: [2, 2, 3.5], fov: 50 }}>
        <ambientLight intensity={0.7} />
        <spotLight intensity={0.5} angle={0.1} penumbra={1} position={[10, 15, 10]} castShadow />
        <Suspense fallback={null}>
          <Model position={[0,0,0]}
       />
          <Environment preset="city" />
          <ContactShadows position={[0, -0.8, 0]} opacity={0.55} scale={10} blur={1.5} far={0.8} />
        </Suspense>
        <OrbitControls
         minPolarAngle={Math.PI/8} 
         maxPolarAngle={Math.PI/2} 
         enableZoom={false}
          enablePan={false} 
          
          />
 
      </Canvas>
      <Picker />
    </div>
  )
}

