import React from 'react'
import { Link } from 'react-router-dom'
import './nav.css'


function Nav() {
    return (



        <div class="main-wrapper">
            <ul class="dot-menu">
                <li><Link to="/">Aqua </Link></li>
                <li><Link to="/red">Crimson </Link></li>
                <li><Link to="/green">Mint </Link></li>
                <li><Link to="/custom">Custom</Link></li>
            </ul>
        </div>




    )
}

export default Nav